/* Navigation

------------------------------------------------------------------- */
#navigation {
    border-bottom: 1px solid $grey-color-light;
}
#logo {
    // background: #ff0;
    float: left;
}
#logo a {
    display: inline-block;
}
#phlow {
    margin: .35em 0 0 0;
    height: 1.85em;
    width: 6em;
    padding: 0;
}
.dropdowns nav,
.dropdowns ul,
.dropdowns li,
.dropdowns a  {margin: 0; padding: 0;}

.toggleMenu {
    display:  none;
}
.nav {
    list-style: none;
     *zoom: 1;
}
.nav:before,
.nav:after {
    content: " ";
    display: table;
}
.nav:after {
    clear: both;
}
.nav ul {
    list-style: none;
}
.nav a {
  padding: .5em 1.5em;
}
.nav a:hover {
  background-color: $ci-1;
  color: $ci-2;
}
.nav li {
    position: relative;
}
.nav > li {
    float: left;
}
.nav > li > a {
    display: block;
}
.nav li  ul {
    position: absolute;
    left: -9999px;
}
.nav > li.hover > ul {
    left: 0;
}
.nav li li.hover ul {
    left: 100%;
    top: 0;
}
.nav li li a {
    background: $ci-2;
    display: block;
    position: relative;
    z-index:100;
}
.nav li li li a {
    z-index:200;
    background: $ci-2;
}

@media screen and (max-width: $medium) {

    #logo {
        text-align: center;
        float: none;
        height: 7em;
        margin: -1.85em 0 -1em 0;
        padding: 0;
    }
    #phlow {
        width: 7em;
        height: 7em;
    }

    a:focus {
        color: #fff;
    }
    .nav ul {
        width: 100%;
    }
    .nav > li {
        float: left;
        border-bottom: 1px solid #ccc;
    }

    ul.nav li {
        background: $ci-1;
    }
    .nav a:hover {
        background-color: lighten($ci-1, 5%);
        color: #fff;
    }
    .nav li {
        background: $ci-1;
    }
    .nav li li {
        background: lighten($ci-1, 20%);
    }
    .nav li li:first-of-type a {
        box-shadow: inset 0px 2px 4px -2px rgba(0, 0, 0, 0.75);
    }
    ul.nav li a {
        color: #fff;
    }
    .active {
        display: block;
    }
    .nav > li {
        float: none;
    }
    .nav > li > .parent {
        background-position: 95% 50%;
    }
    .nav ul {
        display: block;
        width: 100%;
    }
   .nav > li.hover > ul , .nav li li.hover ul {
        position: static;
    }
}

/* togle menu button for narrow screens */
.toggleMenu {
    background: $ci-1;
    line-height: 2.5em;
}

/* small screens menu button */
a.toggleMenu {
    width:100%;
    text-align: center;
    color: #fff;
}
