/* Up To Top
------------------------------------------------------------------- */

#up-to-top {
  margin: 6em 0 0.25em;
}
#up-to-top a {
  border: 0;
}
#up-to-top a svg {
  -webkit-transition: all .15s cubic-bezier(.33,.66,.66,1);
  transition: all .15s cubic-bezier(.33,.66,.66,1);
}
#up-to-top svg {
  fill: $grey-color-light;
}
#up-to-top a:hover svg {
  fill: $ci-1;
}
