@charset "utf-8";

/* Functions
------------------------------------------------------------------- */
@import "functions";


/* Global Variables
-------------------------------------------------------------------

These settings are needed to set the breakpoints for typography
and to adjust the masthead

*/

$global-radius:     3px;

$small:  640px;
$medium: 1024px;
$large:  1440px;


/* Color Variables
-------------------------------------------------------------------- */

$ci-1                               : #aa0132;   // phlow-rotbraun
$ci-2                               : #fabb00;   // phlow-orange
$ci-3                               : #005380;   // phlow-dunkel-blau 
$ci-4                               : #82cbd0;   // phlow-hell-blau
// Alerts
$alert-color                        : #aa0132;   // phlow-rotbraun
$success-color                      : #00792c;   // phlow-dunkel-gruen
$warning-color                      : #e05a10;   // phlow-orange
$info-color                         : #005380;   // phlow-dunkel-blau
// Grey Scale
$grey-color-super-light             : #f8f8f8;
$grey-color-light-light             : #f0f0f0;
$grey-color-light                   : #ccc;
$grey-color                         : #4b4b4b;
$grey-color-dark                    : #323232;
// Basics
$text-color                         : #111;
$background-color                   : #fefefe;
$background-light-color             : $grey-color-light-light;
$line-color                         : $grey-color-light;
$topbar-navigation-background-color : 'rgba(#fefefe, 0.8)';   // background-color
$topbar-navigation-link-color       : #005380;
$topbar-navigation-link-hover-color : #82cbd0;
$topbar-navigation-border-bottom    : 1px solid #ccc;
$footer-color                       : #828282;

/* Settings
------------------------------------------------------------------- */
@import "normalize";


/* Typography
------------------------------------------------------------------- */

/* Font Families */
$base-font-family                  : Georgia, "Times New Roman", serif;
$font-family-serif                 : Georgia, "Times New Roman", serif;
$font-family-sans                  : "Open Sans", "Neue Helvetica", Arial, serif;
$font-family-mono                  : "Inconsolata", "Courier New", Monaco, monospace;
$header-font-family                : "Open Sans", "Neue Helvetica", Arial, serif;
$header-font-family-h1             : "Open Sans Condensed", "Open Sans", "Neue Helvetica", Arial, serif;

/* Font Sizes */
$base-font-size                    :    16px;
$base-font-size-medium-screen      :    18px;
$base-font-size-large-screen       :    20px;
$font-size-p                       :     1em;
$h1                                : 2.281em;
$h2                                : 1.802em;
$h3                                : 1.602em;
$h4                                : 1.424em;
$h5                                : 1.266em;
$h6                                : 1.125em;
$font-size-small                   :   .85em;

/* Line-height */
$base-line-height: 1.65;

/* Font Weight */
$base-font-weight     : normal;
$font-weight-normal   : normal;
$font-weight-bold     : bold;


@import "typography/general_typography",
        "typography/font_import",
        "typography/typography_article",
        "typography/typography_css_elements",
        "typography/links",
        "typography/syntax_highlighting",
        "typography/typography_media_queries";


/* Layout
------------------------------------------------------------------- */
// Don't hide scrollbar – Makes the logo jump
html {
  overflow: -moz-scrollbars-vertical;
  overflow-y: scroll;
}

body {
  background-color: $background-color;
}

// IMPORTANT FOR FOUNDATION GRID
html,
body {
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit; }

@import "layout/grid_xy",
		    "layout/navigation",
		    "layout/masthead",
		    "layout/logo",
        "layout/search",
        "layout/content",
        "layout/meta",
        "layout/footer",
        "layout/up_to_top",
        "layout/layout_media_queries",
        "layout/alerts",
        "layout/toc",
        "layout/side-nav",
        "layout/buttons",
        "layout/_responsive_video",
        "layout/layout_elements",
        "layout/inline_list",
        "layout/layout_introduction_page",
        "layout/full_width_background_image",
        "layout/card",
        "layout/comments",
        "layout/visibility",
        "layout/cookieconsent",
        "layout/svg_icons";
